* {
  font-family: 'Roboto Condensed',微軟正黑體;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: bold;
}

p {
  letter-spacing: 0.5px;
  line-height: 1.7em;
}

html, body {
  overflow-x: hidden;
}

html.loading {
  max-height: 100vh;
  overflow: hidden;
}

section {
  min-height: 100vh;
  z-index: 5;
  background-color: #F6F6F6;
  color: #222;
  padding-top: 4%;
  padding-bottom: 4%;
  position: relative;
  overflow: hidden;
}

section[data-theme=red] {
  color: #F6F6F6;
  background-color: #C92028;
}

section[data-theme=red] .page_title h1, section[data-theme=red] .page_title h3 {
  color: #F6F6F6;
}

section[data-theme=red] .page_title h3:after {
  border-bottom-color: #F6F6F6;
  background: linear-gradient(90deg, transparent 0%, transparent 50%, #F6F6F6 50%, #F6F6F6 100%);
}

section[data-theme=black] {
  color: #F6F6F6;
  background-color: #222;
}

section[data-theme=black] .page_title h1, section[data-theme=black] .page_title h3 {
  color: #F6F6F6;
}

section[data-theme=black] .page_title h3:after {
  border-bottom-color: #F6F6F6;
  background: linear-gradient(90deg, transparent 0%, transparent 50%, #F6F6F6 50%, #F6F6F6 100%);
}

.row {
  z-index: 2;
}

.page_title {
  margin-bottom: 30px;
}

.page_title h1 {
  font-weight: 500;
  font-size: 30px;
  margin-top: 5px;
}

.page_title h3 {
  color: #C92028;
  letter-spacing: 2px;
  font-weight: 500;
  font-size: 20px;
  margin-bottom: 8px;
}

.page_title h3:after {
  content: "";
  display: block;
  display: inline-block;
  margin-left: 12px;
  width: 60px;
  height: 8px;
  background: linear-gradient(90deg, transparent 0%, transparent 50%, #C92028 50%, #C92028 100%);
  border-bottom: solid 2px #C92028;
  transition: 0.5s;
}

.page_title h3:hover:after {
  width: 70px;
}

.loading_page {
  background-color: #222;
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 5000;
  transition: opacity 0.5s;
  text-align: center;
}

.loading_page .loading {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: white;
  letter-spacing: 20px;
  font-family: 微軟正黑體;
}

.loading_page .headphone_text {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: #F6F6F6;
  letter-spacing: 2px;
  top: initial;
  bottom: 25px;
  font-weight: normal;
}

.loading_page .cover_letters {
  position: absolute;
  left: 50%;
  top: 50%;
  height: 80vh;
  width: auto;
  transform: translate(-50%, -50%);
  opacity: 0.8;
  z-index: 1;
  width: 110%;
}

