  color: #F6F6F6;
}

.page_register .map {
  width: 100%;
  height: 100%;
  position: absolute;
  right: -10%;
  top: 10%;
  opacity: 0.5;
  background: url("http://taipeisoundscape.com/img/map-01.svg");
  background-attachment: fixed;
  background-size: 70% auto;
  background-repeat: no-repeat;
  background-position: center center;
}

footer {
  padding: 30px 0px;
  vertical-align: top;
}

footer img {
  height: 50px;
}

footer .col-sm-12 {
  padding-top: 5px;
  padding-bottom: 5px;
}

footer .colname, footer .data {
  display: inline-block;
  font-size: 15px;
}

footer .colname {
  padding-right: 30px;
  font-weight: bold;
  color: #C92028;
  vertical-align: top;
  padding-top: 12px;
}
