
// Fonts
@import url(https://fonts.googleapis.com/css?family=Raleway:100,300,400,600);
@import url(https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.5.2/animate.min.css);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:300,400,700);
@import url(https://cdnjs.cloudflare.com/ajax/libs/normalize/5.0.0/normalize.min.css);

// Variables
@import "variables";

// Bootstrap
@import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap";

$color_white: #F6F6F6;
$color_red: #C92028;
$color_black: #222;
$break_small: 768px;
$break_medium: 960px;
$break_height_medium: 700px;
//
* {
  font-family: 'Roboto Condensed',微軟正黑體;
 }  // border: solid 1px

h1,h2,h3,h4,h5,h6 {
  font-weight: bold; }

p {
  letter-spacing: 0.5px;
  line-height: 1.7em; }

// sassfunction

@mixin ab_center {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%); }

@mixin size($w,$h) {
  width: $w;
  height: $h; }

@mixin flex_center {
  display: flex;
  justify-content: center;
  align-items: center; }


html,body {
  overflow-x: hidden; }

html {
  &.loading {
    max-height: 100vh;
    overflow: hidden; } }

section {
  min-height: 100vh;
  z-index: 5;
  background-color: $color_white;
  color: $color_black;
  padding-top: 4%;
  padding-bottom: 4%;
  position: relative;
  overflow: hidden; }

section[data-theme=red] {
  color: $color_white;
  background-color: $color_red;
  .page_title {
    h1,h3 {
      color: $color_white; }
    h3 {
      &:after {
        border-bottom-color: $color_white;
        background: linear-gradient(90deg,transparent 0%,transparent 50%,$color_white 50%,$color_white 100%); } } } }

section[data-theme=black] {
  color: $color_white;
  background-color: $color_black;
  .page_title {
    h1,h3 {
      color: $color_white; }
    h3 {
      &:after {
        border-bottom-color: $color_white;
        background: linear-gradient(90deg,transparent 0%,transparent 50%,$color_white 50%,$color_white 100%); } } } }


.row {
  z-index: 2; }



.page_title {
  margin-bottom: 30px;
  h1 {
    font-weight: 500;
    font-size: 30px;
    margin-top: 5px; }
  h3 {
    color: $color_red;
    letter-spacing: 2px;
    font-weight: 500;
    font-size: 20px;
    margin-bottom: 8px;
    &:after {
      content: "";
      display: block;
      display: inline-block;
      margin-left: 12px;
      width: 60px;
      height: 8px;
      background: linear-gradient(90deg,transparent 0%,transparent 50%,$color_red 50%,$color_red 100%);
      border-bottom: solid 2px #C92028;
      transition: 0.5s; }
    &:hover {
      &:after {
        width: 70px; } } } }



//------------------------------------
// loading page
.loading_page {
  background-color: #222;
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 5000;
  transition: opacity 0.5s;
  text-align: center;

  .loading {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    color: white;
    letter-spacing: 20px;
    font-family: 微軟正黑體; }

  .headphone_text {
    @include ab_center;
    color: $color_white;
    letter-spacing: 2px;
    top: initial;
    bottom: 25px;
    font-weight: normal; }

  .cover_letters {
    position: absolute;
    left: 50%;
    top: 50%;
    height: 80vh;
    width: auto;
    transform: translate(-50%,-50%);
    opacity: 0.8;

    z-index: 1;
    width: 110%;
    polyline,line,path,polygon {
      stroke-dasharray: 800px;
      stroke-dashoffset: 800px;
      animation: draw_loading 10s 1 both; }

    @for $i from 1 through 1000 {
      polyline:nth-child(#{$i}) {
        animation-delay: $i*0.02s;
        opacity: random()*0.5+0.2; }
      line:nth-child(#{$i}) {
        animation-delay: $i*0.02s;
        opacity: random()*0.5+0.2; } }
    @for $i from 1 through 100 {
      path:nth-child(#{$i}) {
        animation-delay: $i*0.02s;
        opacity: random()*0.5+0.2; }
      polygon:nth-child(#{$i}) {
        animation-delay: $i*4s;
        opacity: random()*0.5+0.2; } } }

  @keyframes draw_loading {
    to {
      stroke-dashoffset: 0; } } }

//-------------------------------------


//首頁固定的影片


#cover_video {
  @include ab_center;
  width: 100%; }

//靜音按鈕
.soundbtn {
  position: fixed;
  right: 45px;
  top: 100px;
  cursor: pointer;
  z-index: 1000;
  width: 44px;
  transition: 0.5s;
  @media screen and (max-width: $break_small) {
    right: 25px; }

  rect,polygon {
    transition: 0.5s; }
  &:hover {
    rect,polygon {
      fill: $color_red; }
    &:before {
      opacity: 1; } }

  &.mute {
    rect {
      fill: transparent; } }

  &:before {
    display: block;
    content: "音效";
    @include ab_center;
    left: -30px;
    opacity: 0;
    transition: 0.5s;
    color: $color_white;
    letter-spacing: 2px; } }

//分享按鈕
.sharebtn {
  position: fixed;
  right: 45px;
  top: 160px;
  cursor: pointer;
  z-index: 1000;
  width: 44px;
  transition: 0.5s;
  @media screen and (max-width: $break_small) {
    right: 25px; }

  circle,line {
    transition: 0.5s; }
  &:hover {
    circle {
      fill: $color_red; }
    circle:nth-child(1) {
      stroke: $color_red; }
    line {
      stroke: $color_red; }
    &:before {
      opacity: 1; } }

  &:before {
    display: block;
    content: "分享";
    @include ab_center;
    left: -30px;
    top: 49%;
    opacity: 0;
    transition: 0.5s;
    color: $color_white;
    letter-spacing: 2px; } }

//展開的按鈕
.expandbtn {
  position: fixed;
  right: 50px;
  top: 40px;
  cursor: pointer;
  z-index: 1000;
  width: 40px;
  height: 40px;
  @include flex_center;
  @media screen and (max-width: $break_small) {
    right: 25px; }

  &:before,&:after {
    content: '';
    display: block;
    width: 40px;
    height: 4px;
    top: 47%;
    background-color: #fff;
    position: absolute;
    transition: 0.5s; }

  &:before {
    transform: translateY(6px) rotate(0deg); }

  &:after {
    transform: translateY(-6px) rotate(0deg); }


  &:hover {
    &:before,&:after {
      background-color: $color_red; } }

  &.showing {
    &:before {
      transform: rotate(405deg); }

    &:after {
      transform: rotate(315deg); } }

  &.red {
    &:before,&:after {
      background-color: $color_red;
      &:hover {
        background-color: $color_white; } } } }


.enbtn {
  position: fixed;
  right: 45px;
  top: 220px;
  cursor: pointer;
  z-index: 1000;
  width: 44px;
  transition: 0.5s;
  font-size: 30px;
  padding-left: 5px;
  @media screen and (max-width: $break_small) {
    right: 25px; }

  circle,line {
    transition: 0.5s; }
  &:hover {
    circle {
      fill: $color_red; }
    circle:nth-child(1) {
      stroke: $color_red; }
    line {
      stroke: $color_red; }
    &:before {
      opacity: 1; } }

  &:before {
    display: block;
    content: "中/英";
    @include ab_center;
    left: -30px;
    top: 49%;
    opacity: 0;
    transition: 0.5s;
    font-size: 12px;
    color: $color_white;
    letter-spacing: 2px; } }
//-------------------------------------------------------------------------
//導覽列
nav.index {

  color: $color_white;
  background-color: #222;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  @include size(100%,100%);
  pointer-events: none;
  overflow: hidden;

  opacity: 0;
  transition: opacity 1s;



  #nav_video {
    @include ab_center;
    z-index: -1;
    animation-delay: 1s;
    width: 1440px;
    height: 810px;
 }    // padding-bottom: 56.25%

  .map,.coverword {
    width: 100%; }
  .coverword {
    margin-top: -300px; }

  li {
    opacity: 0;
    transition: 0.5s; }

  &.showing {
    opacity: 1;
    pointer-events: initial;
    li {
      opacity: 1; }
    @for $i from 1 through 4 {
      li:nth-child(#{$i}) {
        transition: opacity 0.5s $i*0.1+s; } } }


  .col-sm-5 {
    height: 500px; }
  .black_cover {
    width: 100%;
    height: 100%;
    background-color: rgba(black,0.7);
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%); }

  hr {
    border: solid 1px;
    @media screen and (max-width: $break_small) {
      width: 70%;
      margin-left: 0px; } }
  h1 {
    font-size: 2.2em;
    font-weight: bold; }


  h3 {
    font-size: 1.5em;
    font-weight: bold;
    margin-bottom: 40px; }

  h5 {
    font-weight: 600;
    font-size: 1.5em;
    &:after {
      content: "";
      display: block;
      display: inline-block;
      margin-left: 12px;
      width: 60px;
      height: 8px;
      background: linear-gradient(90deg,transparent 0%,transparent 50%,$color_red 50%,$color_red 100%);
      border-bottom: solid 2px #C92028;
      transition: 0.5s; }
    &:hover {
      &:after {
        width: 70px; } } }
  p {
    font-size: 0.9em;
    margin-top: 20px;
    letter-spacing: 1.5px;
    font-weight: 100;
    line-height: 1.7em; }

  ul {
    list-style: none;
    padding: 0;
    padding-bottom: 40px;
    li {
      padding: 8px 0px;
      transition: 0.3s;
      cursor: pointer;
      margin-left: -10px;
      padding-left: 10px;
      &:hover {
        background-color: white;
        p,h5 {
          color: black; } } } }
  .nav_contanier {
    padding: 5%; }

  @media screen and (max-height: 600px) {
    overflow-y: scroll;
    height: initial;
    background-color: black;
    .black_cover {
      height: 130%; }
    .nav_contanier {
 } } }      // padding-bottom: 50px


//-------------------------------------------------------------------------
//首頁
.page_index {
  overflow: hidden;
  padding: 0;
  height: 100vh;

  @media screen and (max-width: $break_small) {
    background-image: url(http://taipeisoundscape.com/img/indexbgv.png);
    background-size: auto 100%;
    background-position: center bottom;
    #cover_video {
      display: none; } }



  .change_lang_btn {
    display: inline-block;
    text-decoration: none;
    color: $color_white;
    cursor: pointer;
    border-bottom: solid 2px $color_red;
    padding-bottom: 4px;
    margin-top: 20px;
    margin-left: 10px;

    &:hover {
      color: $color_red; } }


  .titles {
    position: absolute;
    z-index: 4;
    left: 68%;
    color: white;
    top: 30%;

    .eng {
      font-size: 1.3em;
      font-weight: 200;
      letter-spacing: 2px;
      margin-bottom: 0px; }
    .title {
      margin-top: 10px; }
    .explain {
      margin-top: 20px;
      // margin-left: -10px
      font-weight: 500; }
    @media screen and (max-width: $break_medium) {
      top: 88px;
      left: 7%; } }

  .row_cover {
    padding: 0;
    .col-sm-12 {
      padding: 0; }
    .cover_letters {
      @include ab_center;
      z-index: 1;
      width: 110%;
      stroke-dasharray: 8000px;
      stroke-dashoffset: 8000px;
      @media screen and (max-width: $break_small) {
        top: 500px; }
      .st0 {
        fill: none;
        stroke: $color_white;
        stroke-width: 92;
        stroke-miterlimit: 10; } } }

  &.start_animation {
    .eng {
      animation: fadeIn 1s 1s both; }
    .title {
      animation: fadeIn 1s 2s both; }
    .explain {
      animation: fadeIn 1s 3s both; }
    .cover_letters {
      animation: draw 30s 2s 1 both; }
    .maintexts {
      animation: fadeIn 1.5s 2s both; }
    #cover_video {
      animation: fadeIn 5s 3s both; }
    .change_lang_btn {
      animation: fadeIn 1s 3.5s both; } } }


@keyframes draw {
  to {
    stroke-dashoffset: 0; } }
//-------------------------------------------------------------------------
//page_playexplore

.page_explore {
  @media screen and (min-width: $break_small) {
    height: 100vh; }
  @media screen and (max-width: $break_small) {
    min-height: 145vh; }

  .bg {
    @include ab_center;
    position: absolute;
    height: 130%;
    mix-blend-mode: darken;
    opacity: 0.25; }


  .scene_title {
    display: inline-block;
    color: $color_red;
    padding: 5px 10px;
    position: relative;
    h1 {
      font-size: 25px; }
    h4 {
      font-size: 17px; } }

  h1,h4 {
    margin: 0; }
  h1 {
    margin-top: 10px; }
  h4 {
    font-weight: 500; }


  .explore_decoration_text {
    font-weight: 500;
    letter-spacing: 20px;
    font-size: 20px;
    color: $color_red;

    @media screen and (max-width: $break_small) {
      display: none; } }


  .btn_next,.btn_pre {
    width: 0px;
    height: 0px;
    box-sizing: initial;
    cursor: pointer;
    display: inline-block;

    @include ab_center;
    z_index: 10;
 }    // background-color: $color_red

  .btn_next {
    border-top: 12px solid transparent;
    border-bottom: 12px solid transparent;

    border-left: 12px solid $color_red;
    left: initial;
    right: 12px; }
  .btn_pre {
    border-top: 12px solid transparent;
    border-bottom: 12px solid transparent;

    border-right: 12px solid $color_red;
    left: 12px; }

  .col-sm-4 {
 }    // padding-bottom: 30px
  .col-sm-8 {
    height: 50vh;

    @media screen and (max-width: $break_small) {
      height: 60vh; } }

  .top_part {
    vertical-align: middle;
    position: relative;
    width: 100%;
    padding-left: 30px;
    margin-top: 8%; }
  h2 {
    width: 400px; }

  .surfing_dots {
    .dot {
      @include size(10px,10px);
      border-radius: 50%;
      border: solid 2px $color_red;
      display: inline-block;
      margin-right: 10px;
      transition: 0.5s;
      cursor: pointer;
      &.red {
        background-color: $color_red; } } }

  .scene_desciption {
    // padding-right: 20px
    margin-right: -10px;
    margin-top: 10px; }

  .scene {
    width: 110%;
    @include ab_center;
    top: initial;
    left: initial;
    bottom: -25vh;
    right: -7vw;
    transform: none;
    // -webkit-pointer-events: none
    overflow: hidden;
    max-height: 0px;
    transition: opacity 0.5s, max-height 0s 1.1s;

    @media screen and (max-width: $break_small) {
      top: 10px; }


    g[data-name=island_1],g[data-name=island_2],g[data-name=island_3], {
      transition: opacity 0.5s;
      opacity: 0; }

    g[data-name=island_1] {
      transition: 0.5s 0s;
      transform: translate(0px,10px); }
    g[data-name=island_2] {
      transition: 0.5s 0s;
      transform: translate(0px,10px); }
    g[data-name=island_3] {
      transition: 0.5s 0s;
      transform: translate(0px,10px); }


    &.showing {
      g[data-name=island_1],g[data-name=island_2],g[data-name=island_3], {
        opacity: 1; }
      g[data-name=island_1] {
        transition: 0.5s 0.3s;
        transform: translate(0px,0px); }
      g[data-name=island_2] {
        transition: 0.5s 0.45s;
        transform: translate(0px,0px); }
      g[data-name=island_3] {
        transition: 0.5s 0.6s;
        transform: translate(0px,0px); }

      -webkit-pointer-events: initial;
      z-index: 10;
      // display: block
      max-height: 10000px;
      transition: opacity 0.5s, max-height 0s 0s; }



    @keyframes sign_wake {
      0% {
        transform: rotate(0deg); }
      80% {
        transform: rotate(0deg); }
      85% {
        transform: rotate(10deg); }
      90% {
        transform: rotate(-6deg); }
      95% {
        transform: rotate(3deg); }
      100% {
        transform: rotate(0deg); } }

    //驚嘆號閃爍
    g[data-name=sign] {
      transform-origin: center bottom;
      animation: sign_wake 3s infinite; }


    g[data-name*=trigger] {
      cursor: pointer;

      &:hover {
        g[data-name=sign] {
          animation: flash 1s infinite; } } }

    //--------------------------------------------------------------------
    //松山機場-動畫
    //--------------------------------------------------------------------

    @mixin shake($angle) {
      @keyframes shake_#{$angle} {
        0% {
          transform: rotate(0deg); }
        50% {
          transform: rotate($angle); }
        80% {
          transform: rotate(-$angle); }
        100% {
          transform: rotate(0deg); } } }

    @mixin translate($biasx,$biasy) {
      @keyframes trans_#{$biasx}_#{$biasy} {
        0% {
          transform: translate(0px,0px); }
        30% {
          transform: translate($biasx,$biasy); }
        70% {
          transform: translate($biasx,$biasy); }
        100% {
          transform: translate(0px,0px); } } }

    @include shake(4deg);
    @include translate(-3px,4px);
    @include translate(3px,-4px);

    //松山機場-廣播

    g[data-name=trigger_radio] {
      &:hover {
        g[data-name=recorder] {
          transform-origin: center bottom;
          animation: 'shake_4deg' 2s infinite; }
        g[data-name=women] {
          transform-origin: center center;
          animation: 'trans_-3px_4px' 10s infinite; }
        g[data-name=paper1] {
          transform-origin: center center;
          animation: 'trans_3px_-4px' 20s infinite; }
        g[data-name=paper2] {
          transform-origin: center center;
          animation: 'trans_3px_-4px' 20s 1s infinite; } } }


    @include translate(3px,3px);
    //松山機場-行李(中)

    g[data-name=trigger_package] {
      &:hover {
        g[data-name=package_upper] {
          animation: 'trans_3px_3px' 2s infinite; }
        g[data-name=package_lower] {
          animation: 'trans_3px_3px' 2s -0.9s infinite; } } }

    @include translate(0px,-3px);

    //松山機場-行李(下)
    g[data-name=trigger_package_2] {
      &:hover {
        g[data-name=package_white] {
          animation: 'trans_0px_-3px' 2s infinite; }
        g[data-name=package_red] {
          animation: 'trans_0px_-3px' 2s -0.9s infinite; } } }



    @include translate(8px,8px);
    //松山機場-飛機

    g[data-name=trigger_airplane] {
      &:hover {
        g[data-name=airplane] {
          animation: 'trans_8px_8px' 2s infinite; } } }


    rect[data-name=hidden] {
      opacity: 0;
      fill: transparent; }

    //松山機場-人揮手
    g[data-name=trigger_man_goodbye] {
      &:hover {
        path[data-name=hand] {
          transform-origin: left center;
          animation: 'shake_30deg' 4s infinite; }

        g[data-name=child] {
          transform-origin: center bottom;
          animation: 2s 'trans_0px_-3px' 1s infinite; } } }
    //--------------------------------------------------------------------
    //龍山寺-動畫
    //--------------------------------------------------------------------

    @include translate(-5px,6px);
    @include shake(30deg);
    @include shake(2deg);


    //龍山寺 把杯
    g[data-name=trigger_man_lucky] {
      &:hover {
        g[data-name=lucky_box] {
          animation: airplane_moving 2s infinite; } } }

    g[data-name=trigger_women_lucky] {
      &:hover {
        g[data-name=women_upper] {
          animation: 'shake_4deg' 5s 1;
          transform-origin: center bottom; }
        g[data-name=lucky_left] {
          animation: 'shake_30deg' 2s infinite;
          transform-origin: center center; }
        g[data-name=lucky_right] {
          animation: 'shake_30deg' 2s 0.5s infinite;
          transform-origin: center center; } } }


    @keyframes opacity {
      0% {
        opacity: 0; }
      60% {
        opacity: 1; }
      80% {
        opacity: 1; }
      100% {
        opacity: 0; } }

    //抽籤

    g[data-name=trigger_man_lucky] {
      g[data-name=dialog] {
        opacity: 0; }

      &:hover {
        g[data-name=man_hand] {
          transform-origin: left center;
          animation: 'shake_4deg' 5s infinite; }

        g[data-name=luckybox] {
          transform-origin: center bottom;
          animation: 'shake_4deg' 2s infinite; }

        g[data-name=luckybar] {
          transform-origin: center center;
          animation: 'shake_2deg' 1s infinite; }

        g[data-name=luckybar]:nth-child(1) {
          animation-delay: -0.3s; }
        g[data-name=luckybar]:nth-child(2) {
          animation-delay: -0.6s; }
        g[data-name=luckybar]:nth-child(3) {
          animation-delay: -0.7s; }
        g[data-name=luckybar]:nth-child(4) {
          animation-delay: -0.9s; }
        g[data-name=luckybar]:nth-child(5) {
          animation-delay: -1.1s; }

        g[data-name=dialog] {
          transform-origin: center center;
          animation: 'opacity' 1s 3s 1 both; } } }



    //抽籤

    g[data-name=trigger_watching_poem] {

      &:hover {
        g[data-name=word] {
          animation: opacity 1s 0s infinite both; }
        @for $i from 1 through 8 {
          g[data-name=word]:nth-child(#{$i}) {
            animation-delay: $i*0.5s; } } } }



    @keyframes scale_fire {
      0% {
        transform: scaleY(1); }
      50% {
        transform: scaleY(1.1); }
      100% {
        transform: scaleY(1); } }

    //香爐
    g[data-name=trigger_temple_fire] {
      &:hover {
        g[data-name=stick_big] {
          transform-origin: center bottom;
          animation: scale_fire 4s 0s infinite both; }

        g[data-name=stick_small] {
          transform-origin: center bottom;
          animation: scale_fire 2s 0s infinite both; }

        @for $i from 1 through 20 {
          g[data-name=stick_small]:nth-child(#{$i})軌跡 {
            animation-delay: random()*-2s; } } } }

    //----------
    //東門

    @include translate(6px,3px);

    g[data-name=trigger_icecream] {
      g[data-name=babu] {
        transform-origin: center center; }
      &:hover {
        g[data-name=hand] {
          transform-origin: left center;
          animation: 2s shake_4deg infinite; }
        g[data-name=babu] {
          animation: 1s shake_4deg infinite; }
        g[data-name=icecream] {
          transform-origin: center bottom;
          animation: 4s shake_4deg infinite; }
        g[data-name=cover] {
          animation: 4s "trans_6px_3px" 1s infinite; } } }




    @keyframes bake_cookie {
      0% {}
      40% {
        transform: rotate(5deg); }
      50% {
        transform: rotate(-10deg); }
      70% {
        transform: rotate(-12deg); }
      90% {
        transform: rotate(-10deg); }
      100% {
        transform: rotate(5deg); } }

    @keyframes bake_cookie_locus {
      1% {
        opacity: 0; }
      2% {
        opacity: 1; }
      10% {
        opacity: 1; }
      11% {
        opacity: 0; }
      100% {
        opacity: 0; } }

    @keyframes now_cookie {
      0% {
        opacity: 1; }
      49% {
        opacity: 1; }
      50% {
        opacity: 0; }
      90% {
        opacity: 0; }
      91% {
        opacity: 1; } }



    g[data-name=trigger_cookie] {
      &:hover {
        g[data-name=man_upper] {
          transform-origin: center bottom;
          animation: 2s shake_4deg -1.5s infinite; }

        g[data-name=now_cookie] {
          animation: 2s now_cookie 0s infinite both; }
        g[data-name=cookie1] {
          animation: 2s bake_cookie_locus -1s infinite both; }
        g[data-name=cookie2] {
          animation: 2s bake_cookie_locus -0.8s infinite both; }
        g[data-name=cookie3] {
          animation: 2s bake_cookie_locus -0.6s infinite both; }
        path[data-name=cookie4] {
          animation: 2s bake_cookie_locus -0.4s infinite both; }

        g[data-name=hand] {
          transform-origin: left center;
          animation: 2s bake_cookie infinite; }
        g[data-name=child] {
          transform-origin: center bottom;
          animation: 2s 'trans_0px_-3px' 1s infinite; }
        g[data-name=mother] {
          transform-origin: center bottom;
          animation: 4s shake_4deg 1s infinite; } } }



    // g[data-name=throw_r]
    //   transform-origin: center center
    //   animation: throwing 1s infinite


    @keyframes throwing {
      0% {
        transform: translateY(0px); }
      50% {
        transform: translateY(0px) rotate(45deg); }
      100% {
        transform: translateY(0px); } }


    @include translate(5px,0px);

    //----------
    //小巨蛋兒
    // +shake(6deg)
    //hand
    @include translate(6px,0px);
    g[data-name=trigger_peoples] {
      &:hover {
        g[data-name=people1] {
          animation: 2.5s "trans_6px_0px" -1.5s infinite; }
        g[data-name=people2] {
          animation: 1.5s "trans_6px_0px" -0.5s infinite; }
        g[data-name=people3] {
          animation: 3s "trans_6px_0px" -1s infinite; }
        g[data-name=people4] {
          animation: 2.4s "trans_6px_0px" -2s infinite; } } }

    g[data-name=trigger_sell_light] {
      &:hover {
        g[data-name=hand] {
          transform-origin: left center;
          animation: 2s shake_4deg -1.5s infinite; } } }

    @include translate(-10px,5px);
    //hand
    g[data-name=trigger_egg_bus] {
      &:hover {
        g[data-name=bus] {
          animation: 5s "trans_-10px_5px" 0s infinite; } } } } }



