//page_playfround
.page_impromptu {

  .bg {
    position: absolute;
    left: -10px;
    top: 0px;
    height: 100%;
    @include ab_center; }
  .bg2 {
    @include ab_center;
    position: absolute;
    width: 140%;
    mix-blend-mode: darken;
    opacity: 0.25; }

  select {
    display: none; }

  .btn_container {
    text-align: center;
    h5 {
      color: $color_red;
      @media screen and (min-width: $break_medium) {
        white-space: nowrap; } } }
  ul {
    list-style: none;
    padding: 0px;
    margin-top: 40px;
    height: 60vh;
    overflow-y: scroll;

    @media screen and (max-width: $break_small) {
      margin-top: 0px; }
    li {
      padding: 5px;
      padding-top: 30px;
      padding-left: 10px;
      border: solid 2px $color_red;
      position: relative;
      font-weight: bold;
      font-size: 14px;
      cursor: pointer;

      &:before,&:after {
        content: "";
        display: block;

        background-color: $color_red;
        @include size(6px,12px);
        border-radius: 0px 6px 6px 0px;
        @include ab_center; }

      &:before {
        left: 0; }
      &:after {
        left: initial;
        right: -2px;
        border-radius: 6px 0px 0px 6px; }

      &.red {
        background-color: $color_red;
        color: $color_white;
        &:before,&:after {
          background-color: $color_white; } } } }

  .xs_switcher {
    margin-top: 20px; }
  .xs_switch {
    border: solid 2px $color_red;
    cursor: pointer;
    text-align: center;
    background-color: transparent;
    transition: 0.5s;
    padding: 10px 0px;
    &.red {
      background-color: $color_red;
      color: $color_white; } }

  .pg_audio_player {
    display: none; }
  .pg_btn {
    cursor pointer {}
    background-color: $color_red;
    display: inline-block;
    border-radius: 50%;
    position: relative;
    cursor: pointer;
    @include size(50px,50px);
    img {
      width: 30px;
      @include ab_center;
      z-index: 5; }
    &:before {
      display: block;
      content: "";
      position: absolute;
      border-radius: 50%;
      @include size(60px,60px);
      @include ab_center;
      border: solid 1px $color_red;
      z-index: 1;
      transition: 0.5s; }

    &:hover {
      // background-color: $color_white
      &:before {
        @include size(70px,70px); } } }

  .btn_container {
    display: inline-block;
    margin-top: 30px; }

  h3 {

    margin-top: 15px;
    .pg_icon {
      display: none; }
    @media screen and (min-width: $break_small) {
      white-space: nowrap;
      display: inline-block; } }


  h4.col_title {
    display: flex;
    align-items: center;
    height: 30px;
    margin-bottom: 0px;
    margin-top: 30px; }

  .vc {
    display: inline-block; }
  //音量大小球球
  .reddot {
    background-color: $color_red;
    display: inline-block;
    margin-left: 10px;
    border-radius: 50%; }

  //svg icon
  .pg_icon {
    width: 50px; }
  //slider
  .volume_sliders {
    background-color: $color_red;
    color: $color_white;
    // padding: 10px
    .row {
      padding: 0px 20px; }
    h4 {
      margin: 0;
      font-size: 20px; }
    input {
      position: absolute;
      top: 50%;
      width: 90%;

      @include ab_center; }
    .col-xs-2 {
      padding: 0;
      height: 50px; }
    .col-xs-10 {
      height: 50px; } }


  input[type=range] {
    -webkit-appearance: none /* Hides the slider so that custom slider can be made */;
    width: 90% /* Specific width is required for Firefox. */;
    background: transparent /* Otherwise white in Chrome */; }



  input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none; }


  input[type=range]:focus {
    outline: none /* Removes the blue border. You should probably do some kind of focus styling for accessibility reasons though. */; }


  input[type=range]::-ms-track {
    width: 100%;
    cursor: pointer;

    /* Hides the slider so custom styles can be added */
    background: transparent;
    border-color: transparent;
    color: transparent; }

  input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    border: 2px solid $color_red;
    @include size(15px,15px);
    border-radius: 50%;
    background: #ffffff;
    cursor: pointer;
    margin-top: -7.5px /* You need to specify a margin in Chrome, but in Firefox and IE it is automatic */;
 }    // box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d /* Add cool effects to your sliders! */

  input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    height: 3px;
    cursor: pointer;
    background: $color_white;
    border-radius: 1.3px; } }


